@use '../util' as *;

.toggle {
    border: none;

    &__wrapper {
        height: rem(24);
    }

    input[type='radio']{
        opacity: 0;
    }

    &__background {
        display: block;
        height: 100%;
        background: linear-gradient(255deg, var(-toggle-bg-end) 0%, var(-toggle-bg-start) 98.02%);
    }

    &__button {
        width: rem(16);
        height: rem(16);
        border-radius: 15%;
        background-color: var(--toggle);
    }

    label {

    }
}